<template>
  <div id="index">
    <div id="header">
      <audio id="myAudio" src="https://www.beidoulab.work:5557/location/public/baojing-01.mp3" ref="myAudio">普通报警</audio>
      <audio id="keyAudio" src="https://www.beidoulab.work:5557/location/public/key.mp3" loop="true" ref="keyAudio">一键报警</audio>
      <div id="logo" v-if="mediaNone">
        <!-- <img src="../../static/images/logo_mobile.png" style='width:100%;' alt class="logoImg" /> -->
        <!-- <img src="../../static/images/logo.png" alt class="logoImg" /> -->
        <p class="logoText">北斗监控平台</p>
      </div>
      <div id="menu">
        <el-menu router class="el-menu-demo" :default-active="activeMenu" mode="horizontal" background-color="#31478F" text-color="#FFFFFF" active-text-color="white">
          <template v-for="n in navList">
            <el-menu-item :key="n.index" v-show="!n.roles||n.roles.indexOf(userInfo.Power)!==-1" :index="n.path">
              <div v-if="topBarImg[n.name]"><img :src="topBarImg[n.name]"></div>
              <div>{{n.name}}</div>
            </el-menu-item>

          </template>
        </el-menu>

        <!-- <el-menu router class="el-menu-demo" :default-active="$route.path" mode="horizontal" background-color="#31478F" text-color="#FFFFFF" active-text-color="rgba(60,126,254,1)">
          <template v-for="n in navList">
          
            <el-menu-item v-if="n.children==undefined" :key="n.index" v-show="n.show" :index="n.path">{{n.name}}</el-menu-item>
            <el-submenu v-else :index="n.index" :key="n.index" v-show="n.show">
              <template slot="title">{{n.name}}</template>
              <template v-for="m in n.children">
               
                <el-menu-item v-if="m.children==undefined" v-show="m.show" :key="m.index" :index="m.path">{{m.name}}</el-menu-item>
