                  <template slot="title">{{m.name}}</template>
                
                  <el-menu-item v-for="a in m.children" :key="a.index" :index="a.path" v-show="a.show">{{a.name}}</el-menu-item>
                </el-submenu>
              </template>
            </el-submenu>
          </template>
        </el-menu> -->

      </div>

      <div id="placeholderBox">
        <div id="userName">
          <span v-if="!mediaNone">{{userInfo.UserName}}</span>
          <el-dropdown v-if="mediaNone">
            <div class="el-dropdown-link">
              <div class="headBox">
              </div>
              <div class="name">{{userInfo.UserName}}</div>
              <div class="dropDownDiv">
                <i class="el-icon-arrow-down"></i>
              </div>
