            <el-badge :is-dot="true" v-if="newWaringData.length&&!dialogFormVisible&&mediaNone">
              <img class="alarm0" src="../../static/images/alarm.png" @click="showAlarm" alt />
              <img class="alarm1" src="../../static/images/alarm1.png" @click="showAlarm" alt />
            </el-badge>
            <img class="alarm0" src="../../static/images/alarm.png" @click="showAlarm" v-if="!newWaringData.length&&mediaNone" alt />
            <img class="alarm1" src="../../static/images/alarm1.png" @click="showAlarm" v-if="!newWaringData.length&&mediaNone" alt />
          </div>
        </div>
        <!-- 我的问答 -->

        <el-badge :is-dot="myQusData.length>0" class="l-my-question" v-if="0==1">
          <el-dropdown trigger="click" @command="handleCommand">
            <span class="el-dropdown-link el-icon-question"></span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item disabled class="l-my-question-item">我的问答</el-dropdown-item>
              <el-dropdown-item v-if="myQusData.length==0" class="l-my-question-noData" disabled>暂时没有问答，可以去帮助看看</el-dropdown-item>
              <template v-else v-for="item in myQusData">
                <el-dropdown-item :command="item" :key="item.id">
                  <div class="l-my-question-cont">
                    <i class="el-icon-s-comment"></i>
                    <div class="line">
                      <span class="tit">我的问题</span>
                      <span class="content">{{item.question}}</span>
                    </div>
                  </div>
                </el-dropdown-item>
              </template>
              <el-dropdown-item divided class="l-my-question-item" command="viewAll">查看全部问答</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-badge>

        <div class="exitBox">
          <img class="exitImg" @click="logout" element-loading-text="正在退出" element-loading-background="rgba(0, 0, 0, 0.8)" v-loading.fullscreen.lock="fullscreenLoading" src="../../static/images/exit.png" alt />
        </div>
      </div>
    </div>

    <!-- 回答问题弹窗 -->
    <el-dialog id="myDialog1" title="我的问答" :visible.sync="anDia" center>
      <el-form ref="anForm" :model="Anqus" label-position="left">
        <el-form-item label="问题:" prop="ques" class="l-textarea">{{Anqus.ques}}</el-form-item>
        <el-form-item label="回答:" prop="cont" class="l-textarea">{{Anqus.cont}}</el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog title="修改密码" :visible.sync="dialogPassFormVisible" :close-on-click-modal="false" center>
      <el-form :model="passForm" status-icon :rules="passRules" ref="passForm" class="formStyle">
        <el-form-item label="旧密码" prop="oldPwd">
          <el-input type="password" v-model="passForm.oldPwd" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="pass">
          <el-input type="password" v-model="passForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="newPwd">
          <el-input type="password" v-model="passForm.newPwd" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitModifyPass">确定</el-button>
      </div>
    </el-dialog>
    <img class="bigAlarm" v-if="newWaringData.length&&newWaringData[0].status==10011&&dialogFormVisible" src="../../static/images/bigAlarm.png" alt />
    <div class="alarmModal" v-if="newWaringData.length&&newWaringData[0].status==10011&&dialogFormVisible"></div>
    <el-dialog v-if="newWaringData.length&&mediaNone" title="报警提示" :show-close="false" :visible.sync="dialogFormVisible" :close-on-click-modal="false" :data="newWaringData" class="elDialog alarmPrompt alarmPrompt1 shake" center :modal="false" :class="{'alarmColor':(newWaringData[startAlarmIndex].status==10011)}" top="6vh">
      <div class="alarmImg">
        <img src="../../static/images/alarmImg.png" alt />
      </div>
      <div class="mmBox">
        <div class="minimizeBox" @click="hideDialog()">
          <i class="el-icon-minus myicons"></i>
        </div>
        <div class="maxmizeBox" @click="dialogFormVisible=false">
          <i class="el-icon-close myicons"></i>
        </div>

      </div>

      <div class="formBoxAlarm" v-if="newWaringData.length">
        <div class="followBox">
          <div class="alarmLabel">{{formatedWarnData.carOrGunType=='车'?'车牌号':'枪号'}}</div>
          <div class="alarmVal">{{carAndGunObj[newWaringData[startAlarmIndex].imei]?carAndGunObj[newWaringData[startAlarmIndex].imei].code:''}}</div>
          <div class="followBtn" @click="follow(newWaringData[startAlarmIndex])" v-show="$store.state.realTimeMonitor.carAndGunObj[newWaringData[startAlarmIndex].imei]&&newWaringData[startAlarmIndex].imei!==$store.state.realTimeMonitor.followCarOrGun">跟踪</div>
          <div class="followBtn" @click="follow(null)" v-show="$store.state.realTimeMonitor.carAndGunObj[newWaringData[startAlarmIndex].imei]&&newWaringData[startAlarmIndex].imei==$store.state.realTimeMonitor.followCarOrGun&&$store.state.realTimeMonitor.followCarOrGun!==null">跟踪中</div>
        </div>

        <div>
          <div class="alarmLabel">设备号</div>
          <div class="alarmVal">{{newWaringData[startAlarmIndex].imei}}</div>
        </div>
        <div>
          <div class="alarmLabel">报警类型</div>
          <div class="alarmVal">{{newWaringData[startAlarmIndex].unusual}}</div>
        </div>
        <div>
          <div class="alarmLabel">开始时间</div>
          <div class="alarmVal">{{newWaringData[startAlarmIndex].info.formatTime}}</div>
        </div>
      </div>
      <el-dialog title="报警处理"  :visible.sync="innerVisible" append-to-body :close-on-click-modal="closeOnClickModal" class="alarmDealBox" center>
        <el-form ref="dataForm" :model="temp" label-position="left" :rules="rules">
          <el-form-item label="处理意见" prop="info" class="info">
            <el-input type="textarea" v-model="temp.info " :autosize="{ minRows: 6}" />
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="handleAlarmSubmit()">确定</el-button>
        </div>
      </el-dialog>
      <el-dialog title="延迟报警" :visible.sync="innerDelayVisible" append-to-body :close-on-click-modal="closeOnClickModal" class="alarmDelayBox" center>
        <el-form ref="delayForm" :rules="rules" :model="delayTemp" label-position="left">
          <el-form-item label="延迟时间(分钟)" prop="time">
            <el-input v-model.number="delayTemp.time" />
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="delayOneAlarmSubmit()">确定</el-button>
        </div>
      </el-dialog>
      <div slot="footer" class="dialog-footer">
        <div class="pageBox">
          <div @click="previousItem">
            <img src="../../static/images/lastPage.png" alt />
          </div>
          <div>{{(startAlarmIndex+1)+'/'+newWaringData.length}}</div>
          <div @click="nextItem">
            <img src="../../static/images/nextPage.png" alt />
          </div>
        </div>
        <div class="handleBox">
          <div @click="cancleAlarm(startAlarmIndex)" v-show="newWaringData[startAlarmIndex].status==10011">取消报警</div>
          <!-- <div @click="handleAlarm(startAlarmIndex)">处理</div> -->
          <div @click="handleAlarm(startAlarmIndex)" v-show="newWaringData[startAlarmIndex].info.uuid">处理</div>
          <div @click="delayOneAlarm(startAlarmIndex)" v-show="newWaringData[startAlarmIndex].info.uuid">延迟</div>
        </div>
      </div>
    </el-dialog>
    <el-dialog class="talkDialog" title="对讲请求" top="20vh" :close-on-click-modal="false" :visible.sync="dialogTableVisibleTalk" :modal="false">
      <el-table :data="talkData" height="400" @selection-change="handleSelectionChangeTalk" :row-key="getTalkRowKey">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column property="deviceNo" label="设备号"></el-table-column>
        <el-table-column property="licenseNo" label="车牌号"></el-table-column>
        <el-table-column property="times" label="次数"></el-table-column>
        <el-table-column property="time" label="时间">
          <template slot-scope="scope">
            {{formatDate(scope.row.time)}}
          </template>
        </el-table-column>
        <!-- <el-table-column property="status" label="状态"></el-table-column> -->

      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click.native.prevent="receiveTalk()">接受</el-button>
        <el-button @click.native.prevent="deleteRow()">拒绝</el-button>
      </div>
    </el-dialog>
    <el-dialog class="vipCheckDialog" :modal-append-to-body='false' v-dialogDrag title="提示" :modal="false" :close-on-click-modal="false" :show-close="false" center :visible.sync="vipCheckDialog" width="24%">
      车辆监控主机模组和枪支定位地图模组授权即将到期，请尽快续费.....
    </el-dialog>

    <span class="shrink1 el-icon-s-fold" v-show="menuPop&&(userInfo.Power==1||userInfo.Power==2||userInfo.Power==this.$userAdminPower)&&(this.$route.path!=='/userManagement'&&this.$route.path!=='/commonProblem'&&this.$route.path!=='/deviceTest'&&this.$route.path!=='/realTimeData')" @click="menuPopClick(0)"></span>
    <span class="shrink1  el-icon-s-unfold" v-show="!menuPop&&(userInfo.Power==1||userInfo.Power==2||userInfo.Power==this.$userAdminPower)&&(this.$route.path!=='/userManagement'&&this.$route.path!=='/commonProblem'&&this.$route.path!=='/deviceTest'&&this.$route.path!=='/realTimeData')" @click="menuPopClick(1)"></span>
    <div class="mainContent">
      <!--左侧导航菜单-->
      <!-- <div class="leftNav" :class="{leftToshow1:!menuPop}" v-show="leftNav.length&&(userInfo.Power==1||userInfo.Power==2||userInfo.Power==8||userInfo.Power==this.$userAdminPower)&&(this.$route.path!=='/userManagement'&&this.$route.path!=='/commonProblem'&&this.$route.path!=='/deviceTest'&&this.$route.path!=='/realTimeData')">
        <el-menu :class="{leftToshow1:!menuPop}" router class="el-menu-demo" :unique-opened="true" :default-active="$route.path" background-color="white" text-color="#000000" active-text-color="rgba(60,126,254,1)">
          <template v-for="n in leftNav">

            <el-menu-item v-if="n.children==undefined" :key="n.index" v-show="n.show" :index="n.path">{{n.name}}</el-menu-item>
            <el-submenu v-else :index="n.index" :key="n.index" v-show="n.show">
              <template slot="title">{{n.name}}</template>
              <template v-for="m in n.children">

                <el-menu-item v-if="m.children==undefined" v-show="m.show" :key="m.index" :index="m.path">{{m.name}}</el-menu-item>
                <el-submenu v-else :index="m.index" :key="m.index">
                  <template slot="title">{{m.name}}</template>

                  <el-menu-item v-for="a in m.children" :key="a.index" :index="a.path" v-show="a.show">{{a.name}}</el-menu-item>
                </el-submenu>
              </template>
            </el-submenu>
          </template>
        </el-menu>
      </div> -->
      <div class="leftNav" :class="{leftToshow1:!menuPop}" v-show="leftNav.length">
        <el-menu :class="{leftToshow1:!menuPop}" router class="el-menu-demo" :unique-opened="true" :default-active="$route.path" background-color="white" text-color="#000000" active-text-color="rgba(60,126,254,1)">
          <template v-for="n in leftNav">

            <el-menu-item v-if="n.children==undefined" :key="n.index" v-show="!n.roles||n.roles.indexOf(userInfo.Power)>-1" :index="n.path">{{n.name}}</el-menu-item>
            <el-submenu v-else :index="n.index" :key="n.index" v-show="!n.roles||n.roles.indexOf(userInfo.Power)>-1">
              <template slot="title">{{n.name}}</template>
              <template v-for="m in n.children">

                <el-menu-item v-if="m.children==undefined" v-show="!m.roles||m.roles.indexOf(userInfo.Power)>-1" :key="m.index" :index="m.path">{{m.name}}</el-menu-item>
                <el-submenu v-else :index="m.index" :key="m.index">
                  <template slot="title">{{m.name}}</template>

                  <el-menu-item v-for="a in m.children" :key="a.index" :index="a.path" v-show="!a.roles||a.roles.indexOf(userInfo.Power)>-1">{{a.name}}</el-menu-item>
                </el-submenu>
              </template>
            </el-submenu>
          </template>
        </el-menu>
      </div>

      <div>
        <router-view :class=[menuShowPop] :key="$route.path" :mediaNone="mediaNone" :menuPop="menuPop" @sidePopChange="sidePopChange" ref="myRouterView"></router-view>

