<script>
import { logsDelay } from "../api/realTimeMonitoring"; //报警延迟
import { alarmHandler, vipCheck } from "../api/alarmList"; //报警处理，由uuid获取id
import { modifypwd } from "../api/user"; //修改密码
import { getToken } from "../utils/auth"; //获取token
import { teamAndCarAllBindList, teamAndGunList } from "../api/realTimeMonitoring"; //大队和车辆列表，大队和枪支列表
import ReconnectingWebSocket from "reconnecting-websocket"; //websoket断线重连
import { quesQuery } from "../api/managementArchives/gunFiles";
import alarmT from "../../static/images/alarmT.png";
import dispatchT from "../../static/images/dispatchT.png";
import fileT from "../../static/images/fileT.png";
import inoutT from "../../static/images/inoutT.png";
import statisticT from "../../static/images/statisticT.png";
import monitorT from "../../static/images/monitorT.png";
import problemT from "../../static/images/problemT.png";
import userT from "../../static/images/userT.png";
import deviceTest from "../../static/images/deviceTest.png";

import pacInouT from "../../static/images/pacInoutT.png";
import taskT from "../../static/images/taskT.png";
import gunInoutT from "../../static/images/gunInoutT.png";
import gunInoutST from "../../static/images/gunInoutST.png";
import carInoutT from "../../static/images/carInoutT.png";
import carInoutST from "../../static/images/carInoutST.png";
import homeT from "../../static/images/home.png";
import gunRecord from "../../static/images/gunRecord.png";
import carRecord from "../../static/images/carRecord.png";
import pacInoutStatisticT from "../../static/images/pacInoutStatistic.png";
import receiveNoticeT from "../../static/images/receiveNotice.png";




export default {

  data() {
    //密码自定义校验
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.passForm.pass !== "") {
          this.$refs.passForm.validateField("password");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.passForm.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      breadList: [],
      activeMenu: '',
      indexBreadcrumbs: [],
      myQusData: [], // 推送过来的问答
      anDia: false, // 问答弹窗
      Anqus: {
        ques: "",
        cont: ""
      }, // 问答内容
      index: 0,
      music: false, //报警声
      mediaNone: true, // 兼容需要隐藏
      fullscreenLoading: false, // 全屏loading
      dialogPassFormVisible: false, //密码验证弹窗
      //修改密码数据
      passForm: {
        oldPwd: "",
        newPwd: ""
      },
      //修改密码验证规则
      passRules: {
        oldPwd: [{ trigger: "blur", required: true, message: "输入旧密码" }],
        pass: [{ validator: validatePass, trigger: "blur", required: true }],
        newPwd: [{ validator: validatePass2, trigger: "blur", required: true }]
      },
      time: 1000, //暂时不知是否可用
      userInfo: {}, //用户信息
      dialogFormVisible: true, //报警弹窗
      closeOnClickModal: false,
      newWaringData: [], //报警数据，是warningDataArr经过处理后的数据
      formLabelWidth: "120px",
      innerVisible: false, //报警处理弹窗显隐
      startAlarmIndex: 0, //报警弹窗报警序号
      //处理报警数据
      temp: {},
      innerDelayVisible: false, //延迟报警弹窗显隐
      delayTemp: {}, //延迟提交数据
      warningDataArr: [], //实际上报警数据
      warningType: null, //报警协议里面的小的报警分类
      int32_cmd: null, //报警协议码
      //报警类型显示
      formatedWarnData: {
        carOrGunType: null
      },
      isClickHide: true, //标志现在是隐藏弹窗状态还是显示弹窗状态（这两个状态是对应是否点击隐藏弹窗按钮）
      showRedCircle: false, //是否显示报警的红点
      carAndGunObj: {}, //车牌号或枪号和设备号的信息
      carAndGunCode: "",
      carOrGunLabel: "",
      uuidObj: {}, //出现在报警弹窗里面的所有报警的uuid
      newWaringIndexData: {}, //待处理或者是延迟的报警的信息
      //延迟验证
      rules: {
        time: [
          {
            required: true,
            type: "number",
            message: "请填写延迟时间",
            trigger: "blur"
          }
        ],
        info: [
          {
            required: true,
            message: "请填写处理意见",
            trigger: "blur"
          }
        ]
      },
      levelList: null,//面包屑
      defaultActiveIndex: '/',
      leftNav: [],
      sidePop: true,
      adarsArr: {
        '400': '前车碰撞预警',
        '401': '道路偏离预警',
        '402': '行人检测预警',
        '403': '驾驶员遮挡或镜头偏离位置',
        // '453':'前车碰撞预警',
        '404': '不系安全带',
        // '454':'前车碰撞预警',
        '618': '疲劳驾驶报警1级',
        '619': ' 疲劳驾驶报警2级',
        '620': ' 接打电话报警1级',
        '621': '接打电话报警2级',
        '622': '抽烟报警1级 ',
        '623': '抽烟报警2级',
        '624': '分神驾驶报警1级',
        '625': '分神驾驶报警2级',
        '626': ' 驾驶员异常报警1级',
        '627': '驾驶员异常报警2级',
        '630': '驾驶员变更事件1级',
        '631': '驾驶员变更事件2级',
      },
      //导航栏对应图标，名字跟图标一一对应
      topBarImg: {
        '首页': homeT, '调度管理': dispatchT, '出入库管理': inoutT, '监控管理': monitorT,
