  created() {

    //避免如网点添加界面刷新以后侧边栏消失的问题
    if (!sessionStorage.leftNav) {
      this.leftNav = [];
    } else {
      this.leftNav = JSON.parse(sessionStorage.leftNav)
    }
    // 控制根据权限不同，用户管理是否显示
    this.userInfo = JSON.parse(this.$store.state.user.userInfo);
    if (this.userInfo.UserName.indexOf('yunnan') !== -1) {
      this.navList[6].roles.pop();
    }
    let power = this.userInfo.Power
    if (this.userInfo.Power == 8) {
      this.navList[7].path = '/carParkingRecord'
    }
    if (this.userInfo.Power == 5) {
      this.navList[7].path = '/networkHandoverRecord'
    }
    this.hasOneShowingChild(this.navList);
    //获取侧边栏
    this.fetchNavData();


    //服务即将到期的弹窗提醒，暂时屏蔽
    // this.getVipCheck()

    //以下代码是处理报警等信息的，除了以下权限其他权限不需要
    if (power !== 1 && power !== 2 && power !== 8 && power !== this.$userAdminPower) {
      return;
    }

    this.$store.dispatch("realTimeMonitor/SAVE_BEIDOUTERMINALMES");

    if (sessionStorage.uuidObj) {
      this.uuidObj = JSON.parse(sessionStorage.uuidObj);
    }
    //需要显示未红色报警状态的设备
    if (!sessionStorage.aloneObj) {
      sessionStorage.setItem("aloneObj", JSON.stringify({}));
    }
    //每次刷新报警数据清空
    if (sessionStorage.warningData) {
      sessionStorage.warningData = "[]";
      this.$store.state.realTimeMonitor.warningData = [];
      this.uuidObj = {};
      sessionStorage.setItem("aloneObj", JSON.stringify({}));
    }
    this.$forceUpdate();
    //处理报警数据用于显示
    if (this.newWaringData) {
      //得到能够用于显示在报警弹窗的报警数据
      this.newWaringData = this.getPopAlarm(
        this.$store.state.realTimeMonitor.warningData
      );

      if (this.newWaringData.length) {
        this.formatedWarnData.carOrGunType = this.formatCarOrGunType(
          this.newWaringData[this.startAlarmIndex]
        );
      }
      for (let i = 0; i < this.newWaringData.length; i++) {
        this.newWaringData[i].unusual = this.getWarningType(
          this.newWaringData[i],
          this.newWaringData[i].type
        );
        this.newWaringData[i].isCancledMes = this.newWaringData[i].isCancled
          ? "已取消"
          : "正在报警";
        let time =
          this.newWaringData[i].info.time ||
          this.newWaringData[i].info.startTime;

        this.newWaringData[i].info.formatTime = this.formatDate(parseInt(time));
      }
    } else {
      this.newWaringData = [];
    }
    //设备在线信息
    if (sessionStorage.getItem("deviceOnline")) {
      this.$store.dispatch(
        "realTimeMonitor/SAVE_DEVICEONLINE",
        JSON.parse(sessionStorage.getItem("deviceOnline"))
      );
    }
    //获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj
    this.getImeiAndNumber();
    // 定义大队、枪支列表的本地存储
    sessionStorage.teamCarList = JSON.stringify({});
    sessionStorage.teamGunList = JSON.stringify({});
  },
  beforeDestroy() {
    this.$store.commit("realTimeMonitor/FOLLOWINFO", null);
    this.$store.commit("realTimeMonitor/FOLLOWCARORGUN", null);
  },
  mounted() {
    // 兼容小屏
    let that = this;

    // that.smScreen();

    // window.addEventListener(
    //   "resize",
    //   () => {
    //     that.smScreen();
    //     // console.log("正在调节浏览器宽度");
    //   },
    //   false
    // );

    if (!sessionStorage.myQustions) {
      // 如果不存在就新建一个对象，用来存储问答
      sessionStorage.myQustions = JSON.stringify([]);
    } else {
      // 存在就复制本地存储的数据到myQusData中，以便推送及弹窗的处理
      this.myQusData = JSON.parse(sessionStorage.myQustions);
    }

    if (this.userInfo.Power !== 1 && this.userInfo.Power !== 2 && this.userInfo.Power !== 8 && this.userInfo.Power !== 4) {
      return;
    }

    //websocket推送开始
    let protojs = require("../proto.js");
    let ws = new ReconnectingWebSocket("wss://www.beidoulab.work:32345");//正式服
    // let ws = new ReconnectingWebSocket("wss://www.beidoulab.work:22345");//测试服

    that.$store.commit("user/WS", ws); //用于保留ws，在退出时关闭
    ws.binaryType = "arraybuffer";

    switch (ws.readyState) {
      case WebSocket.CONNECTING:
        // console.log("正在连接");
        break;
      case WebSocket.OPEN:
        console.log("连接成功");
        break;
      case WebSocket.CLOSING:
        console.log("正在关闭");
        break;

      case WebSocket.CLOSED:
        console.log("已经关闭，或者打开连接失败");
        break;
      default:
        // this never happens
        break;
    }
    let littleEndian = (function () {
      let buffer = new ArrayBuffer(2);
      new DataView(buffer).setInt16(0, 256, true /* littleEndian */);
      // Int16Array uses the platform's endianness.
      return new Int16Array(buffer)[0] === 256;
    })();
    ws.onerror = function (params) {

    };
    ws.onclose = function () {

    };
    ws.onopen = function () {
      // ws.send("Hello Server!");
      let token = getToken();
      let LoginVerifyRequest = protojs.lookupType(
        "com.beidou.proto.share.LoginVerifyRequest"
      );
      let msg = LoginVerifyRequest.create({ bearer: token });

      let buffer = LoginVerifyRequest.encode(msg).finish();

      let bytes = new ArrayBuffer(8 + buffer.length);
      let data = new Uint8Array(bytes, 0, 8);

      let msgLength = new Uint32Array([4 + buffer.length]);
      let msgCmd = new Uint32Array([9999]);

      //这是小端模式写法
      if (littleEndian) {
        msgLength = new Uint8Array(msgLength.buffer).reverse();
        msgCmd = new Uint8Array(msgCmd.buffer).reverse();
      } else {
        //这是大端模式写法
        msgLength = new Uint8Array(msgLength.buffer);
        msgCmd = new Uint8Array(msgCmd.buffer);
      }
      for (let i = 0; i < 4; ++i) {
        data[i] = msgLength[i];
        data[4 + i] = msgCmd[i];
      }

      let contentData = new Uint8Array(bytes, 8, buffer.length);
      for (let i = 0; i < buffer.length; ++i) {
        contentData[i] = buffer[i];
      }
      ws.send(bytes);
    };

    ws.onmessage = function (event) {
      if (event.data instanceof ArrayBuffer) {
        let arraybuffer = event.data;
        let dataView = new DataView(arraybuffer.slice(0, 8));
        let int32_length = dataView.getInt32(0);
        let int32_cmd = dataView.getInt32(4);
        let locationInfo = null;
        let warningArr = null;
        let source = new Uint8Array(arraybuffer.slice(8, int32_length + 4));
        //金库不需要报警等消息推送
        if (that.userInfo.Power == 4 && int32_cmd != 2501 && int32_cmd != 9999) {
          return;
        }
        if (int32_cmd == 10000) {
          //实时位置信息 10000
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.LocationInfoResponse"
          );
          let message = locationInfo.decode(source);
          //存放枪支和车的数据
          that.$store.dispatch("realTimeMonitor/SAVE_NEWSOCKETDATA", {
            status: int32_cmd,
            message: message
          });
          // if(message.imei=='5010653'){
          //   console.warn(message.netPower)
          // }
          let deviceOnline = that.$store.state.realTimeMonitor.deviceOnline;
          if (!deviceOnline.hasOwnProperty(message.imei)) {
            deviceOnline[message.imei] = 1;
            that.$store.dispatch(
              "realTimeMonitor/SAVE_DEVICEONLINE",
              deviceOnline
            );
            //把设备在线状态存起来，然后在created里面重新赋值，解决在刷新页面以后state值回复初始状态的问题
            sessionStorage.setItem(
              "deviceOnline",
              JSON.stringify(that.$store.state.realTimeMonitor.deviceOnline)
            );
          }
        } else if (int32_cmd == 10003) {
          //脱离 10003
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.DeviceAloneResponse"
          );
          let message = locationInfo.decode(source);
          // console.warn('10003',message)
          // return;

          that.$store.dispatch("realTimeMonitor/SAVE_NEWSOCKETDATA", {
            status: int32_cmd,
            message: message
          }); //存放枪支和车的数据(单条)

        } else if (int32_cmd == 10001) {
          //车辆上线离线 10001
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.DeviceStatusResponse"
          );
          let message = locationInfo.decode(source);
          that.$store.dispatch("realTimeMonitor/SAVE_NEWSOCKETDATA", {
            status: int32_cmd,
            message: message
          }); //存放枪支和车的数据(单条)

        } else if (int32_cmd == 10002) {
          //gps信号丢失 10002
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.DeviceSignalLosingResponse"
          );
          let message = locationInfo.decode(source);
          that.$store.dispatch("realTimeMonitor/SAVE_NEWSOCKETDATA", {
            status: int32_cmd,
            message: message
          }); //存放枪支和车的数据(单条)

        } else if (int32_cmd == 10004) {
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.NotEnoughGunResponse"
          );
          let message = locationInfo.decode(source);
        } else if (int32_cmd == 10005) {
          //车辆关键点打卡超时 10005
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.CarCheckInTimeoutResponse"
          );
          let message = locationInfo.decode(source);
        } else if (int32_cmd == 10006) {
          //车辆偏移道路协议 10006

          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.CarWarmingResponse"
          );
          let message = locationInfo.decode(source);
          that.$store.dispatch("realTimeMonitor/SAVE_NEWSOCKETDATA", {
            status: int32_cmd,
            message: message
          }); //存放枪支和车的数据(单条)

        } else if (int32_cmd == 10007) {
          // 设备电量报警 10007
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.DeviceAlertResponse"
          );
          let message = locationInfo.decode(source);
          that.$store.dispatch("realTimeMonitor/SAVE_NEWSOCKETDATA", {
            status: int32_cmd,
            message: message
          }); //存放枪支和车的数据(单条)

        } else if (int32_cmd == 10008) {
          //设备破坏报警 10008
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.DeviceBrokenResponse"
          );
          let message = locationInfo.decode(source);
        } else if (int32_cmd == 10009 || int32_cmd == 10011 || int32_cmd == 10012) {
          //车辆报警10009 车辆一键报警10011
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.CarAlarmWarmingResponse"
          );
          let message = locationInfo.decode(source);
          // console.warn('一键报警',message);
          //摄像头损坏和无信号提示
          if (int32_cmd == 10009 && (message.alarmType == 209 || message.alarmType == 54) && message.hasOwnProperty('brokenCam')) {
            let alarmText = "无信号"
            if (message.alarmType == 209) {
              alarmText = "损坏"
            }
            that.$message({
              message: '设备' + message.imei + ' 摄像头' + (message.brokenCam + 1) + alarmText,
              type: "warning",
              duration: 2000
            });
          }
          //ADAS报警
          if (int32_cmd == 10012 && that.adarsArr[message.alarmType]) {
            let code = message.imei;
            if (that.carAndGunObj.hasOwnProperty(message.imei)) {
              code = that.carAndGunObj[message.imei].code
            }
            let mesClick = that.$message({
              // message: code + ' ' + that.adarsArr[message.alarmType],
              dangerouslyUseHTMLString: true,
              type: "warning",
              duration: 3000,
              message: `<span class="messageSpan">${code}  ${that.adarsArr[message.alarmType]}</span>`
            });
            mesClick.$el.querySelector('.messageSpan').onclick = () => {
              that.$router.push('/alarmRecord')
            }
          }

          that.$store.dispatch("realTimeMonitor/SAVE_NEWSOCKETDATA", {
            status: int32_cmd,
            message: message
          });
        } else if (int32_cmd == 10010) {
          // //车辆推断绑定关系 10010
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.RelationBetweenDeviceResponse"
          );

          let message = locationInfo.decode(source);

          that.$store.dispatch("realTimeMonitor/SAVE_NEWSOCKETDATA", {
            status: int32_cmd,
            message: message
          });
        } else if (int32_cmd == 9999) {
          //系统登录协议
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.LoginVerifyResponse"
          );
          let message = locationInfo.decode(source);

          // if (message.success == -1) {
          //   that.$store.dispatch("user/logout").then(() => {
          //     that.$router.push({ path: "/login" });
          //   });
          // }
        } else if (int32_cmd == 20000) {
          // 北斗终端消息接收 20000
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.BeidouTerminalReceiveResponse"
          );
          let message = locationInfo.decode(source);
          let beidouTerminalMes = [];
          if (!sessionStorage.beidouTerminalMes) {
            beidouTerminalMes = [];
          } else {
            beidouTerminalMes = JSON.parse(sessionStorage.beidouTerminalMes);
          }
          beidouTerminalMes.unshift(message);
          sessionStorage.setItem(
            "beidouTerminalMes",
            JSON.stringify(beidouTerminalMes)
          );

          that.$store.dispatch("realTimeMonitor/SAVE_BEIDOUTERMINALMES");
        } else if (int32_cmd == 10050) {
          // 我的问答推送
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.QuestionAnswerResponse"
          );
          let myQus = locationInfo.decode(source);
          // 根据推送过来的id，更新数据
          quesQuery({ id: myQus.id }).then(res => {
            // 相同id先删除再unshift
            that.myQusData.forEach((x, i) => {
              if (x.id == res.data.id) {
                that.myQusData.splice(i, 1);
              }
            });
            that.myQusData.unshift(res.data); // 更新数据
            sessionStorage.myQustions = JSON.stringify(that.myQusData); //同步本地存储
          });
          // 此时如果在帮助页面，应该更新表格内容]
          if (that.$router.currentRoute.name == "commonProblem") {
            that.$refs.myRouterView.getQ();
          }
          // 如果详情弹窗已经打开，是否更新内容？
        } else if (int32_cmd == 2501) {
          locationInfo = protojs.lookupType(
            "com.beidou.proto.realtime.StockManagerInOutInfoResponse"
          );
          that.$store.dispatch("realTimeMonitor/SAVE_PACKAGEINOUTSOCKET", locationInfo.decode(source));
        } else if (int32_cmd == 10030) {//首页车辆报站
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.CarParkingDetailResponse"
          );
          let reportMes = locationInfo.decode(source)
          // console.warn('报站', reportMes)
          that.$store.dispatch("realTimeMonitor/SAVE_CARPARKINGDETAILSOCKET", reportMes);
        } else if (int32_cmd == 10060) {//对讲请求推送
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.CarDeviceTalkCallResponse"
          );
          let talkMes = locationInfo.decode(source)


          // for (let i = 0; i < that.talkData.length; i++) {
          //   if (that.talkData[i].deviceNo == talkMes.deviceNo) {
          //     that.$set(that.talkData[i], 'times', ++that.talkData[i].times);
          //   } else {
          //     talkMes.times = 1;
          //     that.talkData.unshift(talkMes);
          //   }
          // }
          // if (that.talkData.length == 0) {
          //   talkMes.times = 1;
          //   that.talkData.unshift(talkMes);
          // }
          let devIndex = that.talkData.findIndex((item, index) => {
            return item.deviceNo == talkMes.deviceNo;
          })

          if (devIndex !== -1) {
            that.talkData.splice(devIndex, 1);
          }
          that.talkData.unshift(talkMes)
          that.dialogTableVisibleTalk = true;
        } else if (int32_cmd == 2600) {

          locationInfo = protojs.lookupType(
            "com.beidou.proto.realtime.BankSchedulingApplyResponse"
          );
          that.$message.warning('有银行预约任务哦')
        } else if (int32_cmd == 10014) {

          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.CarOfflineResponse"
          );
          let parkMes = locationInfo.decode(source);
          that.$message.warning(parkMes.licenseNo + ' 停车时长：' + that.intervalTime(parkMes.changeTime, new Date()))
        } else if (int32_cmd == 10080) {//枪支出入库未归还报警
          locationInfo = protojs.lookupType(
            "com.beidou.proto.share.GunReturnResponse"
          );
          let message = locationInfo.decode(source);
          that.$store.dispatch("realTimeMonitor/SAVE_NEWSOCKETDATA", {
            status: int32_cmd,
            message: message
          }); 
        

        } else {
          return;
        }

        //处理报警数据 由于各个协议的报警数据结构都不一样，不方便显示和利用数据，因此以下统一对数据进行处理
        //由getWarningData函数统一处理成一样的数据形式
        if (locationInfo && int32_cmd !== 10000) {
          that.int32_cmd = int32_cmd;
          warningArr = locationInfo.decode(source);

          if (sessionStorage.getItem("warningData")) {
            that.warningDataArr = JSON.parse(
              sessionStorage.getItem("warningData")
            );
          }
          if (int32_cmd == 10003) {
            that.warningType = null;
            that.getWarningData(
              that.warningDataArr,
              warningArr.alarms,
              "imei",
              int32_cmd,
              warningArr.canceled
            );
          } else if (int32_cmd == 10004) {
            that.warningType = null;
            that.getWarningData(
              that.warningDataArr,
              warningArr.relation,
              "car",
              int32_cmd
            );
          } else if (int32_cmd == 10005) {
            that.warningType = null;
            warningArr.infos.time = warningArr.infos.startTime;
            that.getWarningData(
              that.warningDataArr,
              warningArr.infos,
              "imei",
              int32_cmd
            );
          } else if (int32_cmd == 10006) {
            // console.warn("车辆偏移",warningArr);
            that.warningType = warningArr.type;
            that.getWarningData(
              that.warningDataArr,
              warningArr.infos,
              "imei",
              int32_cmd,
              warningArr.cancelled
            );
          } else if (int32_cmd == 10007) {
            if (warningArr.alertType == 1) {
              //超速报警暂时先屏蔽
              that.warningType = warningArr.alertType;
              that.getWarningData(
                that.warningDataArr,
                [
                  {
                    type: warningArr.type,
                    imei: warningArr.imei,
                    status: warningArr.alertType,
                    value: warningArr.value,
                    time: warningArr.time
                  }
                ],
                "imei",
                int32_cmd
              );
            }
          } else if (int32_cmd == 10008 || int32_cmd == 10080) {
            that.warningType = null;
            that.getWarningData(
              that.warningDataArr,
              [warningArr],
              "imei",
              int32_cmd
            );
          } else if (int32_cmd == 10009) {
            if (warningArr.alarmType == 300) {
              that.warningType = warningArr.alarmType;
              warningArr.type = 2;
              that.getWarningData(
                that.warningDataArr,
                [warningArr],
                "imei",
                int32_cmd
              );
            }
          } else if (int32_cmd == 10011) {
            if (warningArr.alarmType == 22) {
              that.warningType = warningArr.alarmType;
              warningArr.type = 2;
              that.getWarningData(
                that.warningDataArr,
                [warningArr],
                "imei",
                int32_cmd
              );
            }
          }
        }
      }
    };
  },
  watch: {
    //路由变化时改变侧边栏
    $route() {
      this.fetchNavData();
    },
    innerVisible: function (val, oldval) {
      if (val == false) {
        this.dialogFormVisible = true;
      }
    },
    dialogFormVisible: function (val, oldval) {
      //暂停已经报警声
      if (val == false) {
        this.$refs.keyAudio.pause();
      }
    },
    //监控报警
    "$store.state.realTimeMonitor.warningData": {
      handler: function (newer, older) {
        let that = this;
        if (newer) {
          let newerWarn = JSON.parse(
            JSON.stringify(newer)
          );
          //得到能用于显示的报警数据，这个报警已经排序过，一键报警会排在最前面
          that.newWaringData = this.getPopAlarm(newerWarn);
          if (
            that.startAlarmIndex + 1 > that.newWaringData.length &&
            that.newWaringData.length
          ) {
            that.startAlarmIndex = that.newWaringData.length - 1;
          }
          //避免只修改了车牌号等情况也重新弹出提示窗口
          if (
            that.$store.state.realTimeMonitor.isEditFiles == false &&
            that.$store.state.realTimeMonitor.isAddNewAlarm == true
          ) {
            //如果是一键报警的话即使已经点击隐藏弹窗也应该让弹窗出现
            if (
              that.newWaringData.length &&
              that.newWaringData[0].status !== 10011
            ) {
              that.dialogFormVisible = this.isClickHide;
            }
          }
          //得到显示内容
          for (let i = 0; i < this.newWaringData.length; i++) {
            that.newWaringData[i].unusual = that.getWarningType(
              that.newWaringData[i],
              that.newWaringData[i].type
            );
            that.newWaringData[i].isCancledMes = that.newWaringData[i].isCancled
              ? "已取消"
              : "";
            that.newWaringData[i].info.formatTime = that.formatDate(
              parseInt(that.newWaringData[i].info.time)
            );
          }
          if (that.newWaringData.length > 0) {
            that.formatedWarnData.carOrGunType = that.formatCarOrGunType(
              that.newWaringData[that.startAlarmIndex]
            );
            // 提示
            let mes = newerWarn[0]; // 最新的数据
            that.$message.closeAll();
            if (
              that.carAndGunObj.hasOwnProperty(mes.imei) &&
              that.$store.state.realTimeMonitor.isAddNewAlarm == true
            ) {
              let time1 = 2000;
              if (mes.status == 10011) {
                time1 = 5000;
              }
              that.$message({
                message: that.carAndGunObj[mes.imei].code + " " + mes.unusual,
                type: "warning",
                duration: 2000,
                offset: 1000
              });
            }
          }
          this.$forceUpdate();
        }
      }
    },
    //监测是否编辑了车辆和枪支信息，以便及时更新枪支和车辆信息
    "$store.state.realTimeMonitor.isEditFiles": {
      handler: function (newer, older) {
        if (newer == true) {
          this.getImeiAndNumber();
        }
      }
    },
    //检测报警位于第几条
    startAlarmIndex: function (val, oldval) {
      this.formatedWarnData.carOrGunType = this.formatCarOrGunType(
        this.newWaringData[val]
      );
    }
  },
  methods: {
    // 如果某一级只有一个页面能显示，则把这唯一一个页面提升到上一级，比如调度管理下面如果只有一个工作进度界面，则把工作进度界面替换调度管理
    // 主要根据权限来判定
    hasOneShowingChild(list) {
      list.forEach((item, index) => {
        let showingChildren = item.children && item.children.filter(item1 => {
          if (item1.roles && item1.roles.indexOf(this.userInfo.Power) == -1) {
            return false
          } else {
            if (item1.children) {
              this.hasOneShowingChild(item1.children)
            }
            return true
          }
        })
        // console.warn(showingChildren)
        if (showingChildren && showingChildren.length == 1) {
          list[index] = showingChildren[0];
        }

      })

    },
    getVipCheck() {
      vipCheck()
        .then(res => {
          if (res.status == 0) {
            if (res.data && res.data.alarm) {
              this.vipCheckDialog = true;
              this.expireTime = res.data.expireTime;
            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {

        });
    },
    intervalTime(old_date, new_date) {


      let difftime = (new_date - old_date) / 1000; //计算时间差,并把毫秒转换成秒

      let days = parseInt(difftime / 86400); // 天  24*60*60*1000 
      let hours = parseInt(difftime / 3600) - 24 * days;    // 小时 60*60 总小时数-过去的小时数=现在的小时数 
      let minutes = parseInt(difftime % 3600 / 60); // 分钟 -(day*24) 以60秒为一整份 取余 剩下秒数 秒数/60 就是分钟数
      let seconds = parseInt(difftime % 60);  // 以60秒为一整份 取余 剩下秒数


      return days + "天 " + hours + "时 " + minutes + "分 " + seconds + "秒 "
    },
    getTalkRowKey(row) {
      return row.deviceNo
    },
    receiveTalk() {

      if (this.talkMultipleSelection > 1) {
        this.$message.warning('只能选择一个对讲，请重新选择');
        return;

      }
      if (this.talkMultipleSelection == 0) {
        this.$message.warning('请选择对讲设备')
        return;
      }

      // this.selectTalkData=this.talkMultipleSelection[0];
      sessionStorage.selectTalkData = JSON.stringify(this.talkMultipleSelection[0])
      this.$store.commit("realTimeMonitor/SOCKETTALKDATA", this.talkMultipleSelection[0]);

      if (this.$route.path !== '/realTimeMonitoring') {
        this.$router.push('/realTimeMonitoring');

      }

    },
    deleteRow() {
      for (let j = 0; j < this.talkMultipleSelection.length; j++) {
        for (let i = this.talkData.length - 1; i >= 0; i--) {
          if (this.talkData[i].deviceNo == this.talkMultipleSelection[j].deviceNo) {
            this.talkData.splice(i, 1);
            break;
          }
        }
      }

    },
    handleSelectionChangeTalk(val) {
      this.talkMultipleSelection = val;
    },
    menuPopClick(type) {
      if (type == 0) {
        this.menuPop = false;
        this.menuShowPop = 'menuShowPop'
      } else {
        this.menuPop = true;
        this.menuShowPop = ''

      }
    },
    sidePopChange(val) {
      this.menuPop = val;

    },

    fetchNavData() { // 初始化菜单激活项
      let cur_path = this.$route.path; //获取当前路由
      let power = this.userInfo.Power
      for (let i = 0; i < this.navList.length; i++) {
        let children = this.navList[i].children;
        if (!children && this.navList[i].path === cur_path && this.navList[i].roles.indexOf(power) > -1) {
          this.leftNav = [];
          this.activeMenu = this.navList[i].path;
          break;
        }
        if (children) {
          for (let j = 0; j < children.length; j++) {
            if (children[j].path === cur_path && (!children[j].roles || children[j].roles.indexOf(power) != -1)) {
              this.leftNav = children;
              this.activeMenu = this.navList[i].path;
              break;
            }
            let grand_children = children[j].children;//第一层都会有
            if (grand_children) {
              for (let k = 0; k < grand_children.length; k++) {//第二层
                if (grand_children[k].path === cur_path && (!grand_children[k].roles || grand_children[k].roles.indexOf(power) != -1)) {
                  this.leftNav = children;
                  this.activeMenu = this.navList[i].path;
                  break;
                }
                // 如果该菜单下还有子菜单
                if (children[j].children) {
                  let grandChildren = children[j].children;
                  for (let z = 0; z < grandChildren.length; z++) {
                    if (grandChildren[z].path === cur_path && (!grandChildren[z].roles || grandChildren[z].roles.indexOf(power) != -1)) {
                      this.leftNav = children;
                      this.activeMenu = this.navList[i].path;
                      break;
                    }
                  }
                }
              }
            }
          }
        }
      }
      sessionStorage.leftNav = JSON.stringify(this.leftNav)
    },

    //登出
    logout() {
      this.fullscreenLoading = true;
      setTimeout(() => {
        // 调用登出请求
        this.$store
          .dispatch("user/logout")
          .then(() => {
            this.$router.push({ path: "/login" });
          })
          .catch(err => {
            this.$message.error(err); //登录失败提示错误
          })
          .finally(() => {
            this.fullscreenLoading = false;
          });
      }, 500);
    },

    // 点击确认报警
    confirm() {
      this.dialogFormVisible = false;
    },
    // 上一条
    previousItem() {
      if (this.startAlarmIndex > 0) {
        this.startAlarmIndex -= 1;
      }
    },
    // 下一条
    nextItem() {
      if (this.startAlarmIndex < this.newWaringData.length - 1) {
        this.startAlarmIndex += 1;
      }
    },
    // 点击处理报警按钮
    handleAlarm(index) {
      this.innerVisible = true;
      this.temp = {};
      //得到该被处理的这表报警信息
      this.newWaringIndexData = { ...this.newWaringData[index] };
    },
    // 处理报警提交
    handleAlarmSubmit() {
      let uuid = this.newWaringIndexData.info.uuid;
      let id = this.newWaringIndexData.info.id;
      let tableName = this.formatDate1(parseInt(this.newWaringIndexData.info.time));
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          // if (uuid) {
          let subData = {}
          if (uuid) {
            subData.uuidMap = { [tableName]: [uuid] };
          } else {
            subData.idMap = { [tableName]: [id] };
          }
          subData.handlerInfo = this.temp.info
          alarmHandler(
            // { uuids: { [tableName]: [uuid] }, info: { handlerInfo: this.temp.info } }).then(res => {
            subData).then(res => {

              if (res.status == 200) {
                // this.$message.success(res.msg);
                this.$notify({
                  title: "Success",
                  message: "处理成功",
                  type: "success",
                  duration: 2000
                });
                this.renewWaningData(
                  this.newWaringIndexData,
                  this.newWaringData
                );
                this.startAlarmIndex = 0;
                this.innerVisible = false;
                this.$refs["dataForm"].clearValidate();
                this.$refs["dataForm"].resetFields();
              } else {
                this.$message.error(res.msg);
              }
            });
          // } else {
          //   this.$message.error("不存在uuid");
          //   return;
          // }

          // sessionStorage.SetItem("warningData", JSON.stringify(this.newWaringData));
        } else {
          return false;
        }
      });
    },
    cancleAlarm(index) {
      this.newWaringIndexData = { ...this.newWaringData[index] };
      this.$refs.keyAudio.pause();
      this.renewWaningData(this.newWaringIndexData, this.newWaringData);
      if (this.newWaringData.length && (this.newWaringData[0].status == 10011) && (this.$refs.keyAudio.paused == false)) {
        this.$refs.keyAudio.pause(); //如果关闭了所有一键报警则关闭一键报警声
      }
    },
    //函数作用：处理或者延迟报警后更新报警信息和处理跟相关的数据
    renewWaningData(infoData, data) {
      for (let i = 0; i < data.length; i++) {
        if (
          (data[i].info.uuid &&
            infoData.info.uuid &&
            data[i].info.uuid == infoData.info.uuid) ||
          (!data[i].info.uuid &&
            !infoData.info.uuid &&
            data[i].info.time == infoData.info.time &&
            data[i].imei == infoData.imei &&
            data[i].status == infoData.status &&
            data[i].id == infoData.id
          )
        ) {
          if (this.uuidObj.hasOwnProperty(data[i].info.uuid)) {
            delete this.uuidObj[data[i].info.uuid];
          }
          data.splice(i, 1);
          sessionStorage.setItem("warningData", JSON.stringify(data));
          sessionStorage.setItem("uuidObj", JSON.stringify(this.uuidObj));
          this.$store.dispatch("realTimeMonitor/SAVE_WARNINGDATA");
          this.$store.commit("realTimeMonitor/ISADDNEWAlARM", false);
          break;
        }
      }
    },
    //报警类型显示
    getWarningType(row, type) {
      if (row.status == 10001) {
        if (type == 1) {
          return "上线";
        } else {
          return "离线";
        }
      } else if (row.status == 10002) {
        return "GPS信号丢失";
      } else if (row.status == 10003) {
        return "脱离";
      } else if (row.status == 10004) {
        return "车辆周围没有足够的枪支";
      } else if (row.status == 10005) {
        return "车辆关键点打卡超时";
      } else if (row.status == 10006) {
        if (type == 1) {
          return "车辆偏移";
        } else {
          return "车辆路线变动";
        }
      } else if (row.status == 10007) {
        if (type == 1) {
          if (row.info.value !== null && row.info.value !== undefined) {
            return "电量不足 电量:" + row.info.value + "%";
          } else {
            return "电量不足";
          }
        } else {
          if (row.info.value) {
            return "超速 速度:" + row.info.value + "km/h";
          } else {
            return "超速";
          }
        }
      } else if (row.status == 10008) {
        return "设备破坏";
      } else if (row.status == 10009) {
        if (type == 22) {
          return "一键报警";
        } else if (type == 300) {
          return "区域超速";
        }
      } else if (row.status == 10011) {
        if (type == 22) {
          return "一键报警";
        } else if (type == 300) {
          return "区域超速";
        }
      } else if (row.status == 10080) {
        return "枪支未归还报警";
      }
    },
    //格式化时间
    formatDate(timestamp) {
      if (isNaN(timestamp)) {
        return "";
      } else {
        let date = new Date(timestamp);
        let year = date.getFullYear();
        let month = this.addZero(date.getMonth() + 1);
        let day = this.addZero(date.getDate());
        let hours = this.addZero(date.getHours());
        let minutes = this.addZero(date.getMinutes());
        let seconds = this.addZero(date.getSeconds());

        return (
          year +
          "-" +
          month +
          "-" +
          day +
          " " +
          hours +
          ":" +
          minutes +
          ":" +
          seconds
        );
      }
    },
    //格式化时间
    formatDate1(timestamp) {
      if (isNaN(timestamp)) {
        return "";
      } else {
        let date = new Date(timestamp);
        let year = date.getFullYear();
        let month = this.addZero(date.getMonth() + 1);
        let day = this.addZero(date.getDate());


        return (
          year +
          "_" +
          month +
          "_" +
          day
        );
      }
    },
    //补0操作
    addZero(num) {
      return num < 10 ? "0" + num : num;
    },
    //点击延迟报警
    delayOneAlarm(startAlarmIndex) {
      this.$nextTick(() => {
        this.delayTemp = {};
      });
      this.innerDelayVisible = true;
      //得到被延迟的这条信息，以免在报警信息改变时被延迟信息也发送改变从而导致错误
      this.newWaringIndexData = { ...this.newWaringData[startAlarmIndex] };
    },
    //延迟报警提交
    delayOneAlarmSubmit() {
      let uuid = this.newWaringIndexData.info.uuid;
      this.$refs["delayForm"].validate(valid => {
        if (valid) {
          if (uuid) {
            this.delayTemp.uuids = [uuid];
          } else {
            this.$message.error("不存在uuid");
            return;
          }
          logsDelay(this.delayTemp).then(res => {
            if (res.status == 0) {
              //更新报警相关信息操作
              // this.$message.success(res.msg);
              this.$message({
                message: res.msg,
                type: "success"
              });
              this.renewWaningData(this.newWaringIndexData, this.newWaringData);
              this.startAlarmIndex = 0;
              this.innerDelayVisible = false;
              this.$refs["delayForm"].clearValidate();
              this.$refs["delayForm"].resetFields();
            } else {
              this.$message.error(res.msg);
            }
          });
        } else {
          return false;
        }
      });
    },

    //把所有报警信息放在一个数组里   以下是报警数据格式
    // [
    //     {
    //       imei:'3434',
    //       status:10002,
    //       info:{
    // imei:'3434',
    // time:"3543654675",
    // type:'3435465'
    //       },
    //       type:null 这个type用于区分二级报警类型，如区分设备上下线报警中的离线还是上线，10002，10006会有值（10002，10006有子报警类型，例如10006协议type=1时是偏移报警，type=2时是路线变动报警），其他状态都为null
    //     }
    //   ]
    //
    //处理报警数据，由于接收的报警数据格式都不一样，在这做统一处理
    getWarningData(warningData1, warningInfo1, imei, int32_cmd, canceled) {
      let isCancled = false;
      let that = this;
      let warningData = [...warningData1];
      let warningInfo = [...warningInfo1];
      let canceles = [];
      let imeiNum = "imei";
      if (canceled) {
        canceles = [...canceled];
      }
      //这个区分主要是报警协议的设备号不一定是用imei属性表示的，比如10004协议是用car属性
      if (imei == "imei") {
        imeiNum = "imei";
      } else {
        imeiNum = "car";
      }

      //从warningData1里面把已经取消报警的作个标志
      if (canceles.length) {
        let isChange = false; //判断报警是否被改变
        for (let i = 0; i < canceles.length; i++) {
          for (let j = warningData.length - 1; j >= 0; j--) {
            if (
              warningData[j].status == int32_cmd &&
              warningData[j].imei == canceles[i].imei &&
              warningData[j].isCancled == false
            ) {
              warningData[j].isCancled = true;
              isChange = true;

              if (this.uuidObj.hasOwnProperty(warningData[j].info.uuid)) {
                delete this.uuidObj[warningData[j].info.uuid];
              }
              warningData.splice(j, 1);
            }
          }
        }
        if (isChange == true) {
          that.warningDataArr = warningData;

          sessionStorage.setItem(
            "warningData",
            JSON.stringify(that.warningDataArr)
          );
          sessionStorage.setItem("uuidObj", JSON.stringify(this.uuidObj));
          this.$store.dispatch("realTimeMonitor/SAVE_WARNINGDATA");
          this.$store.commit("realTimeMonitor/ISADDNEWAlARM", false); //这个为false代表不想此刻由于实时监控检测到报警数据变化显示报警提示（因为现在只是改变报警状态为取消状态或者是删除了30s内取消报警的报警，不需要再给出报警提示）
        }
      }
      //新增报警
      for (let ii = 0; ii < warningInfo.length; ii++) {
        isCancled = false; //新增的报警是未取消报警状态
        // 给报警信息添加车牌号或者设备号
        if (that.carAndGunObj.hasOwnProperty(warningInfo[ii][imeiNum])) {
          warningInfo[ii].carOrGunCode =
            that.carAndGunObj[warningInfo[ii][imeiNum]].code;
        }
        //如果该条报警有uuid并且没有被记录过，代表是不同的报警，需要添加（并且下面会删除同类型的报警，新增这个报警）
        if (
          warningInfo[ii].uuid &&
          !this.uuidObj.hasOwnProperty(warningInfo[ii].uuid)
        ) {
          this.uuidObj[warningInfo[ii].uuid] = 1;
          sessionStorage.setItem("uuidObj", JSON.stringify(this.uuidObj));
          this.uuidObj = JSON.parse(sessionStorage.uuidObj);
          // that.music = false;
          // setTimeout(() => {
          //   that.music = true;

          // }, 0);
        } else if (
          warningInfo[ii].uuid &&
          this.uuidObj.hasOwnProperty(warningInfo[ii].uuid)
        ) {
          //如果该设备已经存在这个类型的报警
          for (let e = warningData.length - 1; e >= 0; e--) {
            if (
              warningData[e].status == int32_cmd &&
              warningData[e].imei == warningInfo[ii][imeiNum]
            ) {
              warningData[e].recordTime = new Date().getTime();
              that.warningDataArr = warningData;

              sessionStorage.setItem(
                "warningData",
                JSON.stringify(that.warningDataArr)
              );
              this.$store.dispatch("realTimeMonitor/SAVE_WARNINGDATA");
              this.$store.commit("realTimeMonitor/ISADDNEWAlARM", false); //报警记录时间被改变(这个时间只是用来判断如果1分钟后还没有这条报警上报，则自动取消报警，从报警列表删除)
            }
          }

          continue;
        } else if (!warningInfo[ii].uuid) {
          // that.music = false;
          // setTimeout(() => {
          //   that.music = true;
          // }, 0);
        } else {
          continue;
        }

        //电量不足时同一个设备只保留最新的一条
        if (int32_cmd == 10007) {
          if (warningInfo[ii].value !== 0) {
            isCancled = true;
          }
        }
        //如果该设备已经存在这个类型的报警，将原来该设备的这个报警删除，重新添加新的报警(对于没有uuid的来说，是会把最新的报警放到前面，如果有uuid则只是是替换了并修改时间，位置不会改变)
        for (let k = warningData.length - 1; k >= 0; k--) {
          if (
            warningData[k].status == int32_cmd &&
            warningData[k].imei == warningInfo[ii][imeiNum]
          ) {
            delete this.uuidObj[warningData[k].info.uuid];
            sessionStorage.setItem("uuidObj", JSON.stringify(this.uuidObj));
            warningData.splice(k, 1);
          }
        }
        //如果是一键报警，则跳转到第一页,优先播放一键报警声，其他报警声暂停
        if (int32_cmd == 10011) {
          this.startAlarmIndex = 0;
          this.dialogFormVisible = true;
          this.$refs.myAudio.pause(); //其他报警声暂停
          this.$refs.keyAudio.play(); //一键报警播放
        } else {
          if (this.$refs.keyAudio.paused) {
            this.$refs.myAudio.play(); //其他报警播放
          }
        }
        warningData.unshift({
          status: int32_cmd,
          imei: warningInfo[ii][imeiNum],
          info: warningInfo[ii],
          type: that.warningType,
          isCancled: isCancled,
          recordTime: new Date().getTime()
        });
        that.warningDataArr = warningData;

        sessionStorage.setItem(
          "warningData",
          JSON.stringify(that.warningDataArr)
        );

        this.$store.dispatch("realTimeMonitor/SAVE_WARNINGDATA");
        this.$store.commit("realTimeMonitor/ISADDNEWAlARM", true); //此时由于是新的报警，需要报警提示
      }
    },
    //重置修改密码弹窗
    resetTemp() {
      this.passForm = {
        oldPwd: "",
        newPwd: ""
      };
    },
    //点击修改密码
    modifyPass() {
      this.resetTemp();

      this.dialogPassFormVisible = true;
      this.$nextTick(() => {
        this.$refs["passForm"].clearValidate();
      });
      this.$refs["passForm"].resetFields();
    },
    //修改密码提交
    submitModifyPass() {
      this.$refs["passForm"].validate(valid => {
        if (valid) {
          this.passForm.username = this.userInfo.UserName;
          modifypwd(this.passForm).then(res => {
            if (res.status == 200) {
              this.dialogPassFormVisible = false;
              this.$message({
                message: res.msg,
                type: "success"
              });
            } else {
              this.$message.error(res.msg);
            }
          });
        } else {
          return false;
        }
      });
    },
    //设备类型显示
    formatCarOrGunType(data) {
      if (
        data.status == 10001 ||
        data.status == 10002 ||
        data.status == 10003 ||
        data.status == 10007 ||
        data.status == 10008 ||
        data.status == 10080

      ) {
        if (data.info.type == 1) {
          return "枪";
        } else {
          return "车";
        }
      } else {
        return "车";
      }
    },
    //得到需要显示报警数据并排序
    getPopAlarm(newWaringDataOriginal) {
      let newWaringDatNow = [];
      for (let k = 0; k < newWaringDataOriginal.length; k++) {
        newWaringDatNow.push(newWaringDataOriginal[k]);
      }
      //把一键报警排在所有报警的最前面
      return this.sortArr(newWaringDatNow);
    },
    //对报警排序
    sortArr(arr) {
      let newArr;
      newArr = arr
        .filter(res => res.status == 10011)
        .concat(arr.filter(res => res.status !== 10011));
      return newArr;
    },
    //隐藏弹窗
    hideDialog() {
      this.isClickHide = false;
      this.dialogFormVisible = false;
    },
    //显示报警闪烁点
    showAlarm() {
      if (this.newWaringData.length) {
        this.isClickHide = true;
        this.dialogFormVisible = true;
      }
    },
    //删除一条报警
    closedOneAlarm(index) {
      //以下为实时监控报警数据

      let data = JSON.parse(sessionStorage.warningData);
      let alarm = [...this.newWaringData];

      for (let i = 0; i < data.length; i++) {
        if (
          (data[i].info.uuid &&
            alarm[index].info.uuid &&
            data[i].info.uuid == alarm[index].info.uuid) ||
          (!data[i].info.uuid &&
            !alarm[index].info.uuid &&
            data[i].info.time == alarm[index].info.time &&
            data[i].imei == alarm[index].imei &&
            data[i].status == alarm[index].status)
        ) {
          if (this.uuidObj.hasOwnProperty(data[i].info.uuid)) {
            delete this.uuidObj[data[i].info.uuid];
          }
          data.splice(i, 1);
          sessionStorage.setItem("warningData", JSON.stringify(data));
          sessionStorage.setItem("uuidObj", JSON.stringify(this.uuidObj));
          this.$store.dispatch("realTimeMonitor/SAVE_WARNINGDATA");
          this.$store.commit("realTimeMonitor/ISADDNEWAlARM", false);
          break;
        }
      }
    },
    smScreen() {
      // 屏幕兼容
      let winW = document.body.offsetWidth,
        scrollW = document.body.scrollWidth,
        t = this;
      // 暂时取消小屏展示
      if (winW <= 480 && scrollW < 1600) {
        // 小屏处理 false就是小屏下不显示
        t.mediaNone = false; // 需要隐藏的dom
      } else {
        t.mediaNone = true;
      }

      // if (this.navList.length > 2 && this.mediaNone) {
      if (this.userInfo.Power == 1) {
        this.navList[6].show = true; //用户管理
        this.navList[8].show = false; //设备测试
        // this.navList[8].show = true; //帮助
        this.navList[4].children[0].show = true; //报警设置
        // this.navList[4].children[3].show = true; //系统设置
        // this.navList[3].children[4].show = true; //报警屏蔽设置
        // this.navList[5].children[0].children[3].show = true; //枪支异常统计
        // this.navList[5].children[0].children[1].show = true; //枪支SIM卡流量套餐
        // this.navList[5].children[1].children[9].show = true; //车辆异常统计
        // this.navList[5].children[1].children[8].show = true; //车辆SIM卡流量套餐
        this.navList[7].children[0].show = false; //枪支异常统计
        this.navList[5].children[0].children[1].show = false; //枪支SIM卡流量套餐
        this.navList[7].children[1].show = false; //车辆异常统计
        this.navList[5].children[1].children[1].show = true; //设备档案
        this.navList[5].children[1].children[5].show = false; //车辆SIM卡流量套餐
        this.navList[5].children[1].children[6].show = true; //设备代理
        this.navList[5].children[6].show = true; //银行档案
        this.navList[5].children[7].children[1].show = true; //rfid管理
        this.navList[5].children[7].children[2].show = true; //扫描设备管理

        // this.$router.push('/realTimeMonitoring');

      } else if (this.userInfo.Power == 2 || this.userInfo.Power == this.$userAdminPower) {
        this.navList[6].show = true;
        this.navList[8].show = false;
        // this.navList[8].show = true; //帮助
        this.navList[4].children[0].show = false;
        // this.navList[4].children[3].show = false;
        // this.navList[3].children[4].show = false; //报警屏蔽设置
        // this.navList[5].children[0].children[3].show = false; //枪支异常统计
        // this.navList[5].children[0].children[1].show = false; //枪支SIM卡流量套餐
        // this.navList[5].children[1].children[9].show = false; //车辆异常统计
        // this.navList[5].children[1].children[8].show = false; //车辆SIM卡流量套餐
        this.navList[7].children[0].show = false; //枪支异常统计
        this.navList[5].children[0].children[1].show = false; //枪支SIM卡流量套餐
        this.navList[7].children[1].show = false; //车辆异常统计
        this.navList[5].children[1].children[1].show = false; //设备档案

        this.navList[5].children[1].children[5].show = false; //车辆SIM卡流量套餐
        this.navList[5].children[1].children[6].show = false; //设备代理
        this.navList[5].children[6].show = false; //银行档案
        // this.navList[5].children[7].children[1].show = false; //rfid管理
        this.navList[5].children[7].children[2].show = false; //扫描设备管理

        // this.$router.push('/realTimeMonitoring');

      } else if (this.userInfo.Power == 7) { //车管员
        for (let i = 0; i < this.navList.length; i++) {
          if (i == 11 || i == 14 || i == 18) {
            this.navList[i].show = true;
            // this.$router.push('/carInOutProcedure');


          } else {
            this.navList[i].show = false;
          }
        }
      } else if (this.userInfo.Power == 6) {//枪管员
        for (let i = 0; i < this.navList.length; i++) {
          if (i == 10 || i == 13 || i == 17) {
            this.navList[i].show = true;
            // this.$router.push('/gunInOutProcedure');
          } else {
            this.navList[i].show = false;
          }
        }
      }
      else if (this.userInfo.Power == 4) {//库包管理员
        for (let i = 0; i < this.navList.length; i++) {
          if (i == 12 || i == 15) {
            this.navList[i].show = true;
            // this.$router.push('/task');


          } else {
            this.navList[i].show = false;
          }
        }
      } else if (this.userInfo.Power == 8) {//监控中心
        for (let i = 0; i < this.navList.length; i++) {
          if (i == 0 || i == 3 || i == 7) {
            this.navList[i].show = true;
            // this.$router.push('/task');
            if (i == 7) {
              for (let k = 0; k < this.navList[i].children.length; k++) {
                if (k == 10) {
                  this.navList[i].children[k].show = true;
                } else {
                  this.navList[i].children[k].show = false;

                }
              }

            }


          } else {
            this.navList[i].show = false;
          }
        }
      }
      // }
    },
    //获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj
    getImeiAndNumber() {
      let that = this;
      teamAndCarAllBindList({}).then(res => {
        if (res.status == 0) {
          sessionStorage.teamCarList = JSON.stringify(res.data); // 将数据放入存储中
          for (let index in res.data) {
            if (index == "报废车辆") { continue }

            let team = index;
            if (team == "") {
              team = "未分组";
            }
            let item = res.data[index];
            for (let i = 0; i < item.length; i++) {
              if (item[i].serialNo == "") { continue };
              that.carAndGunObj[item[i].serialNo] = {
                imei: item[i].serialNo,
                code: item[i].licenseNo,
                type: 2,
                teamName: team
              };
            }
          }
          let storeaAlarmData = [];
          if (sessionStorage.warningData) {
            storeaAlarmData = JSON.parse(sessionStorage.warningData);
          }
          for (let i = 0; i < storeaAlarmData.length; i++) {
            if (that.carAndGunObj.hasOwnProperty(storeaAlarmData[i].imei)) {
              storeaAlarmData[i].info.carOrGunCode =
                that.carAndGunObj[storeaAlarmData[i].imei].code;
            }
          }

          that.$store.commit("realTimeMonitor/CARANDGUNOBJ", that.carAndGunObj);
          //刷新以后枪号或车牌号可能获取不到，但是检测不到carAndGunObj的更改，因此更新一下数据
          this.$forceUpdate();
        } else {
          this.$message.error(res.msg);
        }
      });
      teamAndGunList({}).then(res => {
        if (res.status == 0) {
          sessionStorage.teamGunList = JSON.stringify(res.data); // 将数据放入存储中
          for (let index in res.data) {
            let team = index;
            if (team == "") {
              team = "未分组";
            }
            let item = res.data[index];
            for (let i = 0; i < item.length; i++) {
              that.carAndGunObj[item[i].iMei] = {
                imei: item[i].iMei,
                code: item[i].code,
                type: 1,
                teamName: team
              };
            }
          }
          let storeaAlarmData = [];
          if (sessionStorage.warningData) {
            storeaAlarmData = JSON.parse(sessionStorage.warningData);
          }
          for (let i = 0; i < storeaAlarmData.length; i++) {
            if (that.carAndGunObj.hasOwnProperty(storeaAlarmData[i].imei)) {
              storeaAlarmData[i].info.carOrGunCode =
                that.carAndGunObj[storeaAlarmData[i].imei].code;
            }
          }
          that.$store.commit("realTimeMonitor/CARANDGUNOBJ", that.carAndGunObj);
          //刷新以后枪号或车牌号可能获取不到，但是检测不到carAndGunObj的更改，因此更新一下数据
          this.$forceUpdate();
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //报警弹窗里点击跟踪的功能
    follow(info) {
      if (info == null) {
        this.$store.commit("realTimeMonitor/FOLLOWCARORGUN", null);
      } else {
        this.$store.commit("realTimeMonitor/FOLLOWCARORGUN", info.imei);
      }
      this.$store.commit("realTimeMonitor/FOLLOWINFO", info);
    },
    handleCommand(command) {
      // 显示问答
      let _t = this;
      if (command == "viewAll") {
        // 跳转到帮助页面，并清空数据
        _t.myQusData = [];
        if (_t.$router.currentRoute.name != "commonProblem") {
          // 不在帮助页面则跳转
          _t.$router.push({ name: "commonProblem" });
        }
      } else {
        // 弹窗显示问答，并删除掉本地存储中的该条数据
        _t.anDia = true;
        _t.Anqus.ques = command.question;
        _t.Anqus.cont = command.answer;
        _t.myQusData.forEach((x, i) => {
          // 删除myQusData中的该条数据
          if (x.id == command.id) {
            _t.myQusData.splice(i, 1);
          }
        });
      }
      sessionStorage.myQustions = JSON.stringify(this.myQusData); // 同步本地存储
    }
  },


};
</script>
