    line-height: 55px;
    padding-left: 27px;
    border: 1px solid #bfbfbf;
    margin: 0 -1px -1px 0;
  }

  .alarmLabel {
    width: 120px;
    background-color: #f5f5f5;
  }
  .alarmVal {
    width: 240px;
    background-color: white;
  }
  .followBox {
    position: relative;
  }
  .followBtn {
    cursor: pointer;
    min-width: 46px;
    height: 20px;
    line-height: 20px;
    background: $theme-color;
    border-radius: 6px;
    color: white;
    position: absolute;
    top: 50%;
    margin-top: -10px;
    right: 10px;
    text-align: center;
    font-size: 12px;
  }
  .mmBox {
    width: 78px;
    height: 32px;
    position: absolute;
    right: 20px;
    top: 9px;

    display: flex;

    justify-content: space-around;

    align-items: center;

    background: #ab0000;
    border-radius: 16px;
  }
  .minimizeBox,
  .maxmizeBox {
    height: 50px;
    line-height: 50px;

    cursor: pointer;
    font-size: 16px;
    & > .el-icon-minus,
    .el-icon-close {
      color: white;
    }
    &:hover > .el-icon-minus {
      color: rgb(199, 211, 235);
    }
    &:hover > .el-icon-close {
      color: rgb(199, 211, 235);
    }
  }
  .minimizeBox {
    margin-right: -10px;
  }
  .maxmizeBox {
    margin-left: -10px;
  }

  &.el-dialog__wrapper {
    bottom: 61px;
    right: 59px;
    top: auto;
    left: auto;
  }
  .el-dialog {
    margin-top: 0 !important;
    width: auto;

    box-shadow: none;

    .el-dialog__header {
      background: linear-gradient(0deg, #ff3c3c, #e02121);
    }

    .el-dialog__body {
      margin: auto;
      width: 360px;
      height: 260px;
      background-color: white !important;
      border-radius: 10px;
      margin-top: 20px;
      padding: 0;
    }
    .el-dialog__footer {
      padding: 0;
    }
  }

  .el-dialog__headerbtn {
    font-size: 20px;
    display: inline-block;
    vertical-align: middle;
    & .el-dialog__close {
      color: #cccccc;
    }
    &:hover .el-dialog__close {
      color: rgba(60, 126, 254, 1);
    }
  }
  .el-dialog__header {
    padding-top: 72px !important;
    padding-bottom: 0 !important;
    .el-dialog__title {
      color: white;
      font-size: 20px;
      margin-left: 30px;
    }
  }
  &.alarmColor {
    color: white;
    .alarmLabel {
      background-color: #ff3f3f;
      color: white;
    }
    .alarmVal {
      background-color: #ffecec;
    }
  }
}

@keyframes glow {
  0% {
    border-color: red;
    box-shadow: 0 0 5px rgba(211, 4, 4, 0.2), inset 0 0 5px rgba(211, 4, 4, 0.1),
      0 1px 0 red;
  }
  100% {
    border-color: rgb(163, 4, 4);
    box-shadow: 0 0 20px rgba(211, 4, 4, 0.6),
      inset 0 0 10px rgba(211, 4, 4, 0.4), 0 1px 0 rgb(163, 4, 4);
  }
}
.is-active .el-submenu__icon-arrow {
