#index {
  background-color: #eaecf2;
  //表格条纹
  .color-row {
    background: rgba(250, 250, 250, 1);
  }
  .noColor-row {
    background: rgba(255, 255, 255, 1);
  }
  #header {
    min-width: 1280px;
    width: 100%;
    height: $headerBarH;
    box-shadow: 0px 0px 19px 2px rgba(0, 111, 225, 0.06);

    background: $theme-color;
    display: flex;

    #logo {
      width: 286px;
      // min-width: 100px;
      box-sizing: border-box;
      padding-left: 57px;

      // padding-left:40px;
      // padding-right:40px;

      img {
        width: 132px;
      }

      display: flex;
      align-items: center;
      box-shadow: inset 2px 0px 19px rgba(0, 111, 225, 0.06);

      .logoText {
        padding-left: 10px;
        font-size: 24px;
        color: white;
        margin: 0;
        font-weight: bold;
      }
    }

    #menu {
      flex: 1;
      .el-menu {
        height: 100%;
        background: rgba(255, 255, 255, 1);
        &.el-menu--horizontal {
          border-bottom: 0;
        }
        .el-menu--horizontal > .el-submenu.is-active .el-submenu__title {
          border-bottom: 2px solid rgb(60, 126, 254);
        }
        li {
          height: 100%;
          font-size: 18px;
          // line-height: $headerBarH;
          font-weight: 500;

          background: rgba(255, 255, 255, 1);

          &.el-menu-item {
            font-weight: 500;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            min-width: 112px;
            border-bottom: none;
            div {
              margin-bottom: -12px;

              height: 58px;
              display: flex;
              justify-content: center;
              align-items: center;
              img {
                width: 50px;
                height: 50px;
              }
            }
          }

          &.is-active {
            font-weight: bold;
            background-color: #1d337d !important;
            .el-submenu__icon-arrow {
              color: rgb(60, 126, 254);
            }
          }
          .el-submenu__title {
            font-size: 18px;
            font-weight: 500;
            height: 100%;
            line-height: $headerBarH;
            i {
              color: white;
              font-size: 16px;
            }
          }
          .is-active .el-submenu__title {
            font-weight: bold !important;
          }
        }
      }
    }

    #placeholderBox {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      .exitBox {
        width: 80px;
        height: 100%;
        &:hover {
          // background: rgba(245, 249, 252, 1);
        }
      }
      .dropDownDiv {
        display: flex;
        height: auto !important;
        line-height: auto !important;
        margin-left: 20px;
        font-size: 12px;
      }
      .dropDownImg {
        margin-left: 13px;
      }
      .headImg {
        width: 36px;
        height: 36px;
        border-radius: 50%;
      }

      & > div {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
      }
      .alarmCircle {
        .alarm0 {
          display: inline-block;
        }
        .alarm1 {
          display: none;
        }
        .alarmCircleInner {
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          &:hover {
            // background-color: #EAECF2;
            .alarm0 {
              display: none;
            }
            .alarm1 {
              display: inline-block;
            }
          }
        }

        width: 80px;
        // margin-right: 28px;
        .el-badge__content.is-fixed.is-dot {
          right: 24px;
          top: 2px;
        }
        div {
          cursor: pointer;
        }
        #ddd {
          background-color: blue;
        }
        #df {
          background-color: red;
          animation: mymove 1s infinite;
          -webkit-animation: mymove 1s infinite; /*Safari and Chrome*/
        }

        @keyframes mymove {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }

        @-webkit-keyframes mymove /*Safari and Chrome*/ {
          from {
            opacity: none;
          }
          to {
            opacity: block;
          }
        }
      }
      #userName {
        // margin-right: 90px;
        padding-right: 10px;
        height: 100%;
        &:hover {
          // background: rgba(245, 249, 252, 1);
        }
        .el-dropdown-link {
          cursor: pointer;
          color: white;
          font-size: 18px;
          display: flex !important;
          justify-content: center;
          align-items: center;
          .headBox {
            height: 36px;
            margin-right: 10px;
          }
          div {
            height: 50px;
            line-height: 50px;
          }
          img {
            vertical-align: center;
            display: inline-block;
          }
        }
      }

      .alarmClock {
        width: 39px;

        .alarItem.item {
          margin-top: 50px !important;
        }

        .el-icon-arrow-down {
          font-size: 14px;
          margin-left: -5px;
        }
      }

      .el-icon-message-solid {
        font-size: 20px;
        color: #ffffff;
      }

      .exitImg {
        color: #ffffff;
        font-size: 20px;
        margin: 0;
        cursor: pointer;
      }
    }
  }

  .formBoxAlarm {
    max-height: 550px;
    overflow: auto;

    .elForm {
      margin-right: 10px;
    }
  }
}

.alarmDealBox {
  .el-form-item__content {
