@media only screen and (max-width: 1000px) {
  // 新UI
  #index {
    #header {
      #logo {
        display: none;
      }
      #menu {
        // 导航
        ul {
          li {
            // 默认不显示
            display: none;
            &:nth-child(1),
            &:nth-child(3),
            &:nth-child(9) {
              // 需要显示的放这里
              display: unset;
            }
          }
        }
      }
      #placeholderBox {
        // 登录信息、退出等
        #userName {
          // 用户名
          margin-right: 0;
          .headBox {
            display: none;
          }
        }
        .alarmCircle,
        .l-my-question {
          // 报警弹窗按钮 // 帮助
          display: none;
        }
      }
    }
    #realTimeMonitoring {
      height: calc(100vh - 50px);
    }
  }
}
